:root {
    --clr-primary-500: #5EEAD4;
    --clr-primary-400: hsl(9, 100%, 64%);
    --clr-primary-300: hsl(177, 48%, 44%);
    --clr-neutral-900: #213462;
    --clr-primary-900: #0e1358;
    --clr-primary-950: hsla(236, 73%, 20%, 0.55);
    --clr-neutral-200: #E2E8F0;
    --clr-neutral-100: hsl(215, 20%, 65%);
  
    --ff-med: 'Inter-Medium',sans-serif;
  
    --fs-xl: 3rem;
    --fs-700: 2.5rem;
    --fs-600: 2.25rem;
    --fs-550: 1.875rem;
    --fs-500: 1.25rem;
    --fs-450: 1.15rem;
    --fs-400: 1rem;
    --fs-350: 0.875rem;
    --fs-300: 0.75rem;
    --fs-200: 0.625rem;
  }
body {
    font-family: 'Arial', sans-serif;
    margin: 0;
    padding: 0;
    background-color: var(--clr-primary-900);
}



.header {
    background-color: --clr-primary-900;
    color: var(--clr-neutral-200);
    padding: 2rem;
    display: flex;
    justify-content: right;
    align-items: center;
    width: 100%;
    height: 3rem;
}

.header button{
    justify-self: baseline;
    margin: .5rem;
    background-color: #b8c6edd6;
    color: var(--clr-neutral-900);
    border-radius: .25rem;
    font-weight: bold;
    border: 1px solid var(--clr-neutral-100);
    width: 4.5rem;
    font-size: var(--fs-300);
}
.header-login-input{
    display: flex;
    justify-content: space-between;
    margin: .5rem;
    padding: 5px;
    border-radius: .5rem;
    width: 15.5rem;
}
.header-login-input label{
    color: var(--clr-neutral-200);
    font-size: var(--fs-300);
    margin-right: 1.5rem;
}
.header-login-input input{
    font-size: var(--fs-300);
    border: none;
    width: 10rem;
    border-radius: .25rem;
    padding: .25rem;
}


/* Navigation and Dark Mode Toggle */
.nav-links {
    list-style: none;
}

.nav-links li {
    display: inline;
    margin-right: .25rem;
}

/* Homepage Styles */
.coral-main-page {
    flex: 1;
    padding: --fs-500;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 720px;
    margin-bottom: 10rem;
    justify-content: center;
}

.coral-homepage-images-list {
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
    grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
    gap: --fs-200;
    
}

.coral-homepage-image-container {
    position: relative;
    display: flex;
    flex-direction: column;
    cursor: pointer;
    padding: .43rem;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.coral-homepage-image-container img {
    width: 130px;
    height: 100px;
    display: block;
    border-radius: .5rem;
    box-shadow: 0 0 10px var(--clr-primary-500);
}

.coral-home-img-label {
    background-color: #14fdee67;
    border-radius: 7px;
    color:var(--clr-neutral-200);
    font-size: var(--fs-400);
    width: 100%;
    border-radius: --fs-200;
    text-align: center;
    margin-top: .25rem;
}

/* Footer Styles */
.footer {
    background-color: --clr-primary-900;
    color: #333;
    padding: --fs-200 --fs-500;
    text-align: center;
    width: 100%;
    height: 3rem;
}

.footer-login {
    background-color: hsla(236, 73%, 20%, 0.35);
    color: #333;
    width: 100%;
    height: 3rem;
}

/* Responsive Design */
@media (max-width: 767px) {
  .header{
    margin-bottom: 5rem;
  }
    .header, .footer {
        flex-direction: column;
        text-align: center;
    }

    .nav-links li {
        display: block;
        margin: --fs-200 0;
    }
    .images-list {
        grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
    }
    .page-images-list {
        grid-template-columns: repeat(2, 1fr); 
    }
}


@media (max-width: 480px) {
    .page-images-list {
        grid-template-columns: 1fr;    
    }
    
}
.coral-main-page-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
/* scroll box images styling*/
.coral-imagescroll-container {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0.5rem;
  }
  
  .box-scroll-images {
    overflow-x: auto;
    white-space: nowrap;
    /* scrollbar-width is supported by Firefox */
    scrollbar-width: thin;
    /* For other browsers, use WebKit-specific properties */
    /* Chrome, Safari, Edge, Opera */
    -webkit-overflow-scrolling: touch;
    /* Firefox */
    scrollbar-color: var(--clr-primary-500) var(--clr-neutral-200);
    border: 1px solid var(--clr-neutral-100);
    padding: 10px 0;
    width: 720px;
    border-radius: 7px;
    margin: 1rem;
}
  
  .box-scroll-images::-webkit-scrollbar {
    height: 8px;
  }
  
  .box-scroll-images::-webkit-scrollbar-track {
    background: var(--clr-neutral-200);
  }
  
  .box-scroll-images::-webkit-scrollbar-thumb {
    background-color: var(--clr-primary-500);
    border-radius: 10px;
    border: 2px solid var(--clr-neutral-200);
  }
  
  .scroll-image-container {
    display: inline-block;
    position: relative;
    margin-right: 10px;
    cursor: pointer;
    box-shadow: 0 0 10px var(--clr-primary-500);
    border-radius: 50%;
  }
  
  .scroll-image-container img {
    display: inline-block;
    width: 150px;
    height: 150px;
    vertical-align: middle;
    border-radius: 50%;
  }
  
  .heart-icon {
    position: absolute;
    top: 5px;
    right: 10px;
    color: white;
    font-size: 24px;
    cursor: pointer;
    opacity: 0.7;
  }
  
  .heart-icon:hover, .heart-icon.favorited {
    opacity: 1;
    color: var(--clr-primary-400);
  }
  
  
  .close-modal {
    position: absolute;
    top: 10px;
    right: 10px;
    cursor: pointer;
    font-size: 24px;
    color: #000;
  }
  
 
  .heart-icon-modal {
    position: absolute;
    top: --fs-200;
    right: 3.75rem;
    color: white;
    font-size: --fs-550;
    cursor: pointer;
  }
  
  .heart-icon-modal.favorited {
    color: red;
  }
  .coral-homepage-imagescroll-container {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0.5rem;
    z-index: 1;
  }
   /* Modal container */
.modal-box {
    position: fixed; /* Position the modal */
    top: 0;
    left: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent background */
    display: flex; /* Center the modal content vertically */
    justify-content: center; /* Center the modal content horizontally */
    align-items: center; /* Center the modal content vertically */
  }
  .modal-img {
    border-radius: .75rem;
    max-width: 90%; /* Limit the maximum width of the image container */
    max-height: 80vh; /* Limit the maximum height of the image container to 90% of viewport height */
    overflow: auto; /* Enable scrolling if the image exceeds the container size */
    box-shadow: 0 0 5px var(--clr-neutral-200);
  }
  
  /* Modal content */
  .modal-content {
    display: flex;
    flex-direction: column;
    background-color: var(--clr-neutral-900); /* White background */
    padding: 2rem; /* Padding */
    border-radius: 5px; /* Rounded corners */
    min-width: 500px;
    min-height: 500px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3); /* Box shadow */
    justify-content: center;
    align-items: center;
  }
  
  /* Modal image */
  .modal-img img {
    min-width: 450px;
    min-height: 450px;
    max-width: 550px; 
  }

  /* Modal close button */
  .modal-button {
    margin-top: 10px; /* Margin from the top */
    padding: 10px 20px; /* Padding */
    background-color: var(--clr-primary-900); /* Button background color */
    color: var(--clr-neutral-100); /* Button text color */
    font-size: var(--fs-400);
    border: none; /* Remove button border */
    border-radius: 5px; /* Rounded corners */
    cursor: pointer; /* Change cursor on hover */
  }
  
  /* Modal close button hover effect */
  .modal-button:hover {
    background-color: var(--clr-primary-950); /* Darker background color on hover */
  }
  .modal-buttons-cell{
    display: flex;
    gap: .5rem;
  }
  .last-edited-cell{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: .5rem;
  }
  .coral-name-cell{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: .5rem;
  }
  .coral-name-label{
    font-size: var(--fs-400);
    color: var(--clr-neutral-200);
  }
.coral-name-value{
    font-size: var(--fs-400);
    color: var(--clr-neutral-200);
}
.modal-info-table{
    display: flex;
    flex-direction: column;
    gap: .5rem;
    margin-top: 1rem;
}

.page-images-list{
    display: grid;
    grid-template-columns: repeat(4, minmax(100px, 1fr)); 
    width: 46rem;
    gap: 1rem; 
    cursor: pointer;
    justify-content: center;
    align-items: center;
    margin:1rem;
}
.page-img-grid {
    margin-top: 1rem;
    width: 130px;
    height: 130px;
    object-fit: cover;
    display: block;
    border-radius: .5rem;
    box-shadow: 0 0 10px var(--clr-primary-500);
}
.page-image-container {
    display: flex;
    flex-direction: column;
    align-items: center; /* Center-aligns the content of the container */
    gap: 1rem;
}
.page-inputbox{
    margin-top: 1rem;
    margin-left: 1rem;
    margin-right: 1rem;
    display: flex;
    gap: 1rem;
    justify-content: center;
    align-items: center;
    align-content: center;
    width: 95%;
}

.file-box{
    margin-top: .5rem;
    border-radius: .5rem;
    align-self: center;
}
.page-inputbox button{
    
    margin-top: .5rem;
    border-radius: 7px;
    background-color:#b8c6edd6;
    color: var(--clr-neutral-900);
    font-weight: 500;
    width: 150px;
    margin-bottom: .5rem;
}
.description-input{
    width: 100%;  
    padding: 10px;
    border-radius: 4px;
    box-sizing: border-box;  
    resize: vertical;  
    overflow: auto;  
}
.page-input-boxes{
    display: flex;
    flex-direction: column;
    word-wrap: wrap;
    width: 100%;
    gap: .25rem;
}
.page-input-box2 {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    gap:6rem;
    margin-top: auto;
}

/* Styling for text areas within .page-input-boxes */
.page-input-boxes textarea {
    width: 100%; /* Ensure textarea takes full width of the container */
    padding: 8px;
    border-radius: 4px;
    resize: vertical; /* Allows vertical resizing only */
    box-sizing: border-box; /* Includes padding and border in the element's total width and height */
    overflow: auto; /* Adds a scrollbar if text exceeds the space */
}

/* Styling for input[type="text"] within .page-input-boxes for consistency */
.page-input-boxes input[type="text"] {
    width: 100%; /* Ensure input takes full width of the container */
    padding: 8px;
    border-radius: 4px;
    box-sizing: border-box; /* Includes padding and border in width and height calculation */
}
.page-main-box{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}